import React from 'react';


export default function FilterPrice(props) {
    const printFilter = []
    for (let i = 5; i <= 30; i+=5) {
        printFilter.push(
            <label className="form-check">
                <input className="form-check-input" type="checkbox"/>
                <span className="form-check-label">
                    Từ {i}00.000đ
</span>
            </label>
        )
    }
    return (
        <form>
            {printFilter}
        </form>
    )
}